export default {
    name: "my_collection",
    data() {
        return {
            collectionList: []
        }
    },
    onShow: function () {
        this.getMyCollections()
    },
    methods: {
        removeMyLoved(item) {
            var _this = this
            uni.request({
                url: _this.$BASE_HOST + 'collection/delete',
                method: 'GET',
                data: {
                    scenic_id: item.id,
                    user_id: uni.getStorageSync('local_userinfo').id
                },
                success: (res) => {
                    if (res.data.code != 20000) {
                        uni.showToast({
                            title: '取消失败!',
                            duration: 2000,
                            icon: 'none'
                        });
                        _this.getMyCollections()
                    } else {
                        _this.getMyCollections()
                    }
                }
            })
        },
        getMyCollections(){
            var _this = this
            uni.request({
                url: _this.$BASE_HOST + 'scenic/getAllMyLovedScenic',
                method: 'GET',
                data: {
                    user_id: uni.getStorageSync('local_userinfo').id
                },
                success: (res) => {
                    _this.collectionList = res.data.data
                    if (_this.collectionList != null) {
                        _this.collectionList.forEach((item, index) => {
                            item.scenicCover = _this.$BASE_HOST + item.scenicCover
                        })
                    }
                }
            })
        },
        toScenicDetail(item) {
            uni.navigateTo({
               url: '../scenic/scenic_detail/scenic_detail?scenic_id='+item.id
            });
        },
        toYuyueDetail(item) {
            uni.navigateTo({
               url: '../scenic/yuyue/yuyue?scenic_id=' + item.id
            });
        },
    }
}
